.loader {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 2px dashed teal;
    animation: loaders 1s linear infinite;
}

@keyframes loaders {
    0% {
        transform: rotate(0deg) scale(1);
    }

    100% {
        transform: rotate(360deg) scale(1.3);
    }
}